.favorite-exercises-container {
  border-radius: 16px;
  height: 100%;
  display: block;
  padding: 16px;
  overflow: hidden;

  .scroll-list-container {
    height: 88%;
    overflow: hidden;

    div {
      .dash-grid-container {
        min-width: 0;
        width: 100%;
        display: grid;
        gap: 16px;
        grid-template-columns: repeat(2, minmax(0, 1fr));
        grid-auto-rows: minmax(100px, 110px);
        padding-right: 10px;

        .grid-item {
          display: flex;
          flex-direction: row;
          gap: 8px;
          align-items: center;
          border-radius: 16px;
          padding: 8px;
          cursor: pointer;
          // -webkit-user-select: none;
          // -moz-user-select: none;
          // -ms-user-select: none;
          // user-select: none;
          // width: 100px;

          .video-container {
            // flex: 1;
            // width: 100%;
            height: 100%;
          }

          .exercise-name {
            // flex: 1;
            // padding: 8px 0;
          }
        }
      }
    }
  }
}
